/*=====================
  3.6  loader CSS start
==========================*/
.loader-wrapper {
    position: fixed;
    top: 0;
    inset: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(var(--white), 1);
    z-index: 99;

    &.hidden {
        display: none;
    }

    .loader {
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        .loader-img {
            width: 300px;
            height: 300px;
        }
    }
}
