.selectMultiple {
  width: 100%;
  position: relative;
}
.selectMultiple select {
  display: none;
}
.selectMultiple > div {
  position: relative;
  z-index: 2;
  padding: 8px 12px 2px 12px;
  border-radius: 8px;
  background-color: rgba(var(--white), 1);
  font-size: 14px;
  min-height: 44px;
  transition: box-shadow 0.3s ease;
}
.selectMultiple > div .arrow {
  right: 0px;
  top: 0;
  bottom: 0;
  cursor: pointer;
  width: 29px;
  position: absolute;
}
[dir=rtl] .selectMultiple > div .arrow {
  right: unset;
  left: 0;
}
.selectMultiple > div .arrow:before, .selectMultiple > div .arrow:after {
  content: "";
  position: absolute;
  display: block;
  width: 2px;
  height: 8px;
  border-bottom: 8px solid rgba(var(--content-color), 1);
  top: 15px;
  transition: all 0.3s ease;
}
.selectMultiple > div .arrow:before {
  right: 15px;
  transform: rotate(-130deg);
}
[dir=rtl] .selectMultiple > div .arrow:before {
  right: unset;
  left: 15px;
}
.selectMultiple > div .arrow:after {
  left: 6px;
  transform: rotate(130deg);
}
[dir=rtl] .selectMultiple > div .arrow:after {
  right: 6px;
  left: unset;
}
.selectMultiple > div span {
  color: #99a3ba;
  display: block;
  position: absolute;
  left: 12px;
  top: 8px;
  line-height: 1.8;
  transition: all 0.3s ease;
  cursor: pointer;
}
[dir=rtl] .selectMultiple > div span {
  left: unset;
  right: 12px;
}
.selectMultiple > div span.hide {
  opacity: 0;
  visibility: hidden;
  transform: translate(-4px, 0);
}
.selectMultiple > div a {
  position: relative;
  padding: 0 25px 10px 10px;
  line-height: 1.8;
  color: rgba(var(--title-color), 1) !important;
  display: inline-block;
  vertical-align: top;
  margin: 0 6px 0 0;
}
[dir=rtl] .selectMultiple > div a {
  margin: 0 0 0 6px;
  padding: 0 10px 10px 25px;
}
.selectMultiple > div a em {
  font-style: normal;
  display: block;
  white-space: nowrap;
}
.selectMultiple > div a:before {
  content: "";
  left: 0;
  top: 0;
  bottom: 6px;
  width: 100%;
  position: absolute;
  display: block;
  background: rgba(var(--box-bg), 1);
  z-index: -1;
  border-radius: 4px;
}
[dir=rtl] .selectMultiple > div a:before {
  left: unset;
  right: 0;
}
.selectMultiple > div a i {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 28px;
  display: block;
}
[dir=rtl] .selectMultiple > div a i {
  left: 0;
  right: unset;
}
.selectMultiple > div a i:before, .selectMultiple > div a i:after {
  content: "";
  display: block;
  width: 1px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  background: rgba(var(--title-color), 1);
  border-radius: 1px;
}
.selectMultiple > div a i:before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.selectMultiple > div a i:after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.selectMultiple > div a.notShown {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.selectMultiple > div a.notShown:before {
  width: 28px;
  transition: width 0.45s cubic-bezier(0.87, -0.41, 0.19, 1.44) 0.2s;
}
.selectMultiple > div a.notShown i {
  opacity: 0;
  transition: all 0.3s ease 0.3s;
}
.selectMultiple > div a.notShown em {
  opacity: 0;
  transform: translate(-6px, 0);
  transition: all 0.4s ease 0.3s;
}
.selectMultiple > div a.notShown.shown {
  opacity: 1;
}
.selectMultiple > div a.notShown.shown:before {
  width: 100%;
}
.selectMultiple > div a.notShown.shown i {
  opacity: 1;
}
.selectMultiple > div a.notShown.shown em {
  opacity: 1;
  transform: translate(0, 0);
}
.selectMultiple > div a.remove {
  pointer-events: none;
}
.selectMultiple > div a.remove:before {
  width: 28px;
  transition: width 0.4s cubic-bezier(0.87, -0.41, 0.19, 1.44) 0s;
}
.selectMultiple > div a.remove i {
  opacity: 0;
  transition: all 0.3s ease 0s;
}
.selectMultiple > div a.remove em {
  opacity: 0;
  transform: translate(-12px, 0);
  transition: all 0.4s ease 0s;
}
.selectMultiple > div a.remove.disappear {
  opacity: 0;
  transition: opacity 0.3s ease 0s;
}
.selectMultiple > ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  z-index: 1;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(var(--white), 1);
  padding: 10px;
  border-radius: 8px;
  transform: translate(0, 20px) scale(0.8);
  transform-origin: 0 0;
  box-shadow: 0 12px 20px rgba(22, 42, 90, 0.08);
  transition: all 0.3s ease-in-out, transform 0.4s cubic-bezier(0.87, -0.41, 0.19, 1.44), filter 0.3s ease 0.2s;
}
[dir=rtl] .selectMultiple > ul {
  left: unset;
  right: 0;
}
.selectMultiple > ul li {
  color: rgba(var(--title-color), 1);
  background-color: rgba(var(--box-bg), 1);
  padding: 10px 15px;
  border-radius: 8px;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease 0.3s, opacity 0.5s ease 0.3s, border-radius 0.3s ease;
}
.selectMultiple > ul li:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  opacity: 0;
  border-radius: 100%;
  transform: scale(1, 1) translate(-50%, -50%);
  transform-origin: 50% 50%;
}
.selectMultiple > ul li.beforeRemove {
  border-radius: 0 0 8px 8px;
}
.selectMultiple > ul li.beforeRemove:first-child {
  border-radius: 8px;
}
.selectMultiple > ul li.afterRemove {
  border-radius: 8px 8px 0 0;
}
.selectMultiple > ul li.afterRemove:last-child {
  border-radius: 8px;
}
.selectMultiple > ul li.remove {
  transform: scale(0);
  opacity: 0;
}
.selectMultiple > ul li.remove:after {
  animation: ripple 0.4s ease-out;
}
.selectMultiple > ul li.show {
  transform: scale(0);
  opacity: 0;
  animation: scale-up 0.35s ease forwards;
}
.selectMultiple.open > div {
  box-shadow: 0 4px 20px -1px rgba(22, 42, 90, 0.12);
}
.selectMultiple.open > div .arrow:before {
  transform: rotate(-50deg);
}
.selectMultiple.open > div .arrow:after {
  transform: rotate(50deg);
}
.selectMultiple.open > ul {
  display: flex;
  align-items: center;
  gap: 5px;
  flex-wrap: wrap;
  transform: translate(0, 12px) scale(1);
  opacity: 1;
  visibility: visible;
  filter: drop-shadow(0 16px 24px rgba(22, 42, 90, 0.16));
}
.selectMultiple.open .empty::before {
  content: "No options available";
  display: block;
  text-align: center;
  font-size: 14px;
  color: rgba(var(--content-color), 1);
}

@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 1;
  }
  25% {
    transform: scale(30, 30);
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: scale(50, 50);
  }
}
@keyframes scale-up {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
