/*=====================
  4.2  Nav CSS start
=======================*/
.navbar-menu {
  position: fixed;
  width: 100%;
  max-width: 600px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 4px 0px 16px 0px rgba(var(--title-color), 0.1);
  z-index: 2;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: rgba(var(--white), 1);
    -webkit-mask-image: radial-gradient(circle at top, transparent 31px, black 32px);
    border-radius: 20px 20px 0 0;
  }

  @media (max-width: 767px) {
    max-width: 100%;
  }

  .scanner-bg {
    position: absolute;
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border-radius: 100%;
    left: 50%;
    top: -50%;
    transform: translate(-50%);
    z-index: 0;

    .scanner-btn {
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(var(--theme-color), 1);
      border-radius: 100%;
      z-index: 1;
      .icon-btn {
        --Iconsax-Color: rgba(var(--white), 1);
      }
    }
  }

  ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;

    &:after,
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: calc(50% - 32px);
      height: 100%;
      z-index: -1;
      background-color: rgba(var(--white), 1);
      border-radius: 20px 20px 0 0;
    }

    &::before {
      left: unset;
      right: 0;
    }

    li {
      position: relative;
      color: rgba(var(--light-text), 1);
      text-align: center;
      width: 20%;

      a {
        color: rgba(var(--light-text), 1);

        .icon {
          width: 20px;
          font-size: 26px;
          margin-left: auto;
          margin-right: auto;
          display: flex;
          justify-content: center;

          .active {
            display: none;
          }

          .unactive {
            display: block;
          }
        }

        h5 {
          margin-top: 5px;
          font-size: 14px;
          font-weight: 500;
          color: rgba(var(--title-color), 1);
        }
      }

      &.active {
        .icon {
          color: rgba(var(--theme-color), 1);
          display: flex;
          justify-content: center;

          .active {
            display: block;
          }

          .unactive {
            display: none;
          }
        }

        h5 {
          font-weight: 500;
          color: rgba(var(--theme-color), 1);
        }
      }
    }
  }

  &.navbar-menu-full {
    background-color: rgba(var(--white), 1);
    box-shadow: 4px 0px 16px 0px rgba(var(--title-color), 0.1);

    &::after {
      content: none;
    }
    ul {
      &:after,
      &::before {
        content: none;
      }
    }
  }
}
