/*=====================
  5.4  categories CSS start
==========================*/
.categories-box {
    width: 60px;
    height: 60px;
    background-color: rgba(var(--box-bg), 1);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;

    .categories-icon {
        --Iconsax-Color: rgba(var(--title-color), 1);
        color: rgba(var(--title-color), 1);
    }
}
.categories-name {
    white-space: nowrap;
}

.categories-listing {
    margin-top: 20px;
    .listing-categories {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        margin-bottom: 15px;
        border: 1px solid rgba(var(--line-color), 1);
        box-shadow: 0px 2px 12px 0px rgba(var(--theme-color), 0.06);
        border-radius: 8px;
        .icon {
            --Iconsax-Color: rgba(var(--title-color), 1);
            --Iconsax-Size: 20px;
        }
        h5 {
            font-weight: 400;
            color: rgba(var(--title-color), 1);
        }

        .form-check {
            width: 100%;
            margin: 0;
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .form-check-label {
                .payment-icon {
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 10px;
                    --Iconsax-Color: rgba(var(--title-color), 1);
                    --Iconsax-Size: 20px;
                    background-color: rgba(var(--box-bg), 1);
                    border-radius: 100%;
                }
            }
            .form-check-input {
                width: 20px;
                height: 20px;
                float: unset;
                margin: 0;
                border: 1px solid rgba(var(--line-color), 1);
                background-color: transparent;
                transition: all 0.3s ease-in-out;

                &:checked {
                    background-color: rgba(var(--theme-color), 1);
                    border-color: rgba(var(--theme-color), 1);
                }
                &:focus {
                    border-color: rgba(var(--line-color), 1);
                    box-shadow: none;
                }
            }

            [type="radio"] {
                border-radius: 100%;
                position: relative;

                &:checked {
                    background-color: rgba(var(--theme-color), 0.4);
                    border: none;
                    &::after {
                        content: "";
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        background-color: rgba(var(--theme-color), 1);
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        border-radius: 100%;
                        transition: all 0.3s ease-in-out;
                    }
                    ~ .form-check-label {
                        font-weight: 600;
                        border-color: rgba(var(--theme-color), 1);
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }

    &.service-listing {
        .listing-categories {
            padding: 12px;
            .form-check {
                .form-check-label {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    cursor: pointer;
                    .categories-icon {
                        width: 40px;
                        height: 40px;
                        border-radius: 6px;
                    }
                    .categories-name {
                        font-weight: 500;
                        font-size: 14px;
                        color: rgba(var(--title-color), 1);
                    }
                }
            }
        }
    }
}

.sub-categories-list {
    display: flex;
    align-items: center;
    gap: 15px;
    padding-inline: 20px;
    overflow: auto;
    li {
        .categories-box {
            .categories-icon {
                width: 24px;
                height: 24px;
                stroke: rgba(var(--title-color), 1);
                fill: transparent;
            }
        }
        .categories-name {
            color: rgba(var(--title-color), 1);
        }
        &.active {
            .categories-box {
                background-color: rgba(var(--theme-color), 0.1) !important;
                border: 1px solid rgba(var(--theme-color), 1);
                .categories-icon {
                    stroke: rgba(var(--theme-color), 1);
                    fill: transparent;
                }
            }

            .categories-name {
                color: rgba(var(--theme-color), 1);
            }
        }
    }
}
