/*==========================
   5.13 onborading CSS start
============================*/
.onboarding-section {
    .main-slider {
        .poster-image {
            text-align: center;
            .image {
                width: 315px;
                height: 315px;
            }
        }
    }
    .bottom-box {
        position: relative;
        text-align: center;
        margin-top: 70px;
        padding: 50px 20px 20px;
        border-radius: 15px;
        background-color: rgba(var(--box-bg), 1);

        .poster-details {
            h2 {
                text-transform: uppercase;
                font-weight: 700;
                color: rgba(var(--title-color), 1);
            }

            p {
                padding: 15px 0;
                font-weight: 400;
                color: rgba(var(--content-color), 1);
            }
        }

        .button-box-group {
            display: flex;
            align-items: center;
            justify-content: space-between;

            [dir="rtl"] & {
                direction: ltr;
            }
            .onboarding-button {
                position: relative;
                inset: unset;
                --Iconsax-Color: rgba(255, 255, 255, 1);
                margin: 0;
                width: 40px;
                height: 40px;
                z-index: 0;
                background-color: rgba(var(--theme-color), 1);
                border-radius: 100%;
                &::after {
                    content: unset;
                }
                &.onboarding-button-prev {
                    background-color: rgba(var(--white), 1);
                    i {
                        --Iconsax-Color: rgba(var(--title-color), 1);
                    }
                }
            }
        }
        .union-img {
            position: absolute;
            top: -125px;
            left: 50%;
            width: 100%;
            height: 100%;
            transform: translateX(-50%);
            z-index: 0;
            filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(29deg) brightness(109%) contrast(102%);
            @media (max-width: 567px) {
                top: -115px;
            }
            @media (max-width: 375px) {
                top: -105px;
            }
            @media (max-width: 345px) {
                top: -125px;
            }
            @media (max-width: 330px) {
                top: -125px;
            }
            body.dark & {
                filter: invert(10%) sepia(17%) saturate(983%) hue-rotate(193deg) brightness(92%) contrast(96%);
            }
        }
    }

    &.servicemen-onboarding-section {
        .auth-form {
            position: absolute;
            width: calc(100% - 40px);
            left: 20px;
            top: 30px;
            max-width: 100%;
        }
        .servicemen-onbording-image {
            .servicemen-img {
                width: 100%;
                height: 800px;
                object-fit: cover;
                @media (max-width: 600px) {
                    height: calc(440px + (800 - 440) * ((100vw - 320px) / (600 - 320)));
                }
            }
        }

        .servicemen-onbording-details {
            margin-top: 40px;
            padding: 20px;
            text-align: center;
            background-color: rgba(var(--box-bg), 1);
            border-radius: 20px;
            h4 {
                text-transform: uppercase;
                font-weight: 700;
                color: rgba(var(--title-color), 1);
            }
            h5 {
                width: 80%;
                margin: auto;
                margin-top: 8px;
                font-weight: 400;
                color: rgba(var(--content-color), 1);
            }
        }
    }
}
