/*========================
  1.1  Variable CSS start
==========================*/
/* font family */
$dm_sans: "DM Sans", sans-serif;

/* color variables */
:root {
  --theme-color: 84, 101, 255;
  --title-color: 0, 22, 46;
  --content-color: 128, 139, 151;
  --box-bg: 245, 246, 247;
  --line-color: 229, 232, 234;
  --secondary-color: 255, 196, 18;
  --success-color: 39, 175, 77;
  --error-color: 255, 75, 75;
  --accent-color: 58, 109, 229;
  --white: 255, 255, 255;
  --black: 0, 0, 0;
}

.dark {
  --theme-color: 84, 101, 255;
  --title-color: 255, 255, 255;
  --content-color: 128, 139, 151;
  --white: 26, 28, 40;
  --black: 255, 255, 255;
  --box-bg: 38, 41, 53;
  --line-color: 58, 61, 72;
}
